import React , {Component} from 'react';
import { Redirect, Link} from 'react-router-dom';
import '../css/css.css';
import $ from 'jquery';
import {setAdmin, setLogin} from "../index";

class Admin extends Component {
    constructor() {
        super();
        this.state = {redirect: false}
    }
    render() {
        let isAdmin = false;
        $.ajax({
            url:"/checkstate",
            context:document.body,
            async:false,
            type:"get",
            success: function(data) {
                if(data === "admin") {
                    isAdmin = true;
                }
            }
        });
        if(isAdmin) {
            setAdmin(true);
        }
        else {
            setAdmin(false);
            alert("Not admin!");
            this.setState({redirect:true});
        }
        if(this.state.redirect) {
            return (
                <Redirect push to="/booklist"/>
            );
        }
        return(
            <div>
                <dl>
                    <dt>Manage books</dt>
                    <dd>Add book</dd>
                    <dd>Update book info</dd>
                    <dd>Delete book</dd>
                    <dd>Query book</dd>
                </dl>
                <dl>
                    <dt>Manage users</dt>
                    <dd>Add user</dd>
                    <dd>Update user info</dd>
                    <dd>Delete user</dd>
                    <dd>Ban user</dd>
                    <dd>Query user</dd>
                </dl>
                <dl>
                    <dt>Sales statistics</dt>
                    <dd>Statistics</dd>
                </dl>
            </div>
        );
    }
}

export default Admin;


